import React from "react";

class Keyboard extends React.Component {
  render() {
    const { onLetterClick, onHintClick } = this.props; // Added onHintClick as a prop
    const keyboardLetters = [
      // Place "hint" next to "á" on the first row, replacing "nope" with an empty string or another key if needed
      ["hint", "á", "é", "í", "ó", "ú", "ü", "invisible"],
      ["q", "w", "e", "r", "t", "y", "u", "i", "o", "p"],
      ["a", "s", "d", "f", "g", "h", "j", "k", "l", "ñ"],
      ["enter", "z", "x", "c", "v", "b", "n", "m", "⌫"],
    ];

    return (
      <div className="keyboard">
        {/* Keyboard Rows */}
        {keyboardLetters.map((row, index) => (
          <div key={index} className="keyboard-row">
            {row.map((letter, index) => {
              if (letter === "hint") {
                return (
                  // Hint button: position it just like a key in the layout
                  <button
                    key={index}
                    className="hint-button"
                    style={{ minHeight: "50px", maxHeight: "50px", minWidth: "80px", maxWidth: "80px" }}
                    onClick={onHintClick}
                  >
                    Hint
                  </button>
                );
              } else if (letter === "invisible") {
                return (
                  // Invisible and unclickable button on the far right
                  <button
                    key={index}
                    className="invisible-button"
                    disabled
                  >
                    {/* Empty button to reserve space */}
                  </button>
                );
              } 
              else {
                return (
                  // Regular keyboard button
                  <button
                    key={index}
                    className={this.findKeyboardClasses(letter)}
                    onClick={() => onLetterClick(letter)}
                  >
                    {letter}
                  </button>
                );
              }
            })}
          </div>
        ))}
      </div>
    );
  }

  // For each letter, check to see if it needs a special/color class
  findKeyboardClasses = (letter) => {
    let keyClasses = "keyboard-key ";
    if (letter === "enter" || letter === "⌫") {
      return (keyClasses += "special-key");
    } else {
      if (this.props.letters.green.includes(letter)) {
        keyClasses += "key-green";
      } else if (this.props.letters.yellow.includes(letter)) {
        keyClasses += "key-yellow";
      } else if (this.props.letters.grey.includes(letter)) {
        keyClasses += "key-grey";
      }
      return keyClasses;
    }
  };
}

export default Keyboard;
