import React from "react";
import Papa from "papaparse";  // Import PapaParse for CSV parsing
import TitleBar from "./TitleBar";
import AlertBox from "./AlertBox";
import PuzzleGrid from "./PuzzleGrid";
import Keyboard from "./Keyboard";

class PuzzleBoard extends React.Component {
  state = {
    letters: {
      green: [],
      yellow: [],
      grey: [],
    },
    solution: "",
    englishSolution: "",
    currentGuess: "",
    pastGuesses: [],
    activeGame: true,
    alerts: [],
    wordList: [],  // Store words loaded from the CSV
    englishList: [],  // Store English translations of the words
    fullList: [],
  };

  validLetters =
    "á é í ó ú ü q w e r t y u i o p a s d f g h j k l z x c v b n m";

  componentDidMount() {
    // focus the screen to accept keyboard input
    this.puzzleBoardInput.focus();
    this.loadWordList();  // Load words from CSV
  }

  // Load the word list from the CSV file
  loadWordList = () => {
    fetch("/SolutionWords.csv")  // Assuming the CSV file is in the public folder
      .then(response => response.text())
      .then(csvData => {
        Papa.parse(csvData, {
          complete: (result) => {
            // Extract Spanish words from the CSV data
            const words = result.data.map(row => row[0]).filter(word => word);
            const englishWords = result.data.map(row => row[1]).filter(word => word); // Only the english words
            this.setState({ wordList: words, englishWords: englishWords }, this.getSolution);
          },
          header: false,  // Since the CSV does not have a header
        });
      })
      .catch(error => {
        console.error("Error loading the CSV file", error);
        this.handleAlert("Error loading solution words.");
      });
      var words = require('an-array-of-spanish-words');
      words = words.filter(word => word.length === 5);
      this.setState({fullList: words});
  };

  // Get a random word from the wordList
  getSolution = async () => {
    const { wordList } = this.state;
    let solution = '';
    if (wordList.length > 0) {
      const randomIndex = Math.floor(Math.random() * wordList.length);
      solution = wordList[randomIndex]; // Get the English translation
      this.setState({ solution });
      console.log('Solution:', solution);
    } else {
      this.handleAlert("No words available.");
      return; // Exit if no words are available
    }
  
    const url = 'https://google-translate113.p.rapidapi.com/api/v1/translator/text';
    const options = {
      method: 'POST',
      headers: {
        'x-rapidapi-key': '0133f24899msha392cf77983e808p1d4947jsn8c6c63150e60',
        'x-rapidapi-host': 'google-translate113.p.rapidapi.com',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        from: 'es', // Automatically detect the language
        to: 'en', // Target language: English
        text: solution, // Use the local variable `solution` here
      }),
    };
  
    try {
      const response = await fetch(url, options);
      if (!response.ok) {
        throw new Error(`Error: ${response.statusText}`);
      }
  
      const result = await response.json();
      console.log('Translation result:', result);
      const englishSolution = result.trans; // Confirm this matches the actual API response structure
  
      this.setState({ englishSolution });
    } catch (error) {
      console.error('Error fetching translation:', error);
      this.handleAlert('Failed to fetch hint. Please try again later.');
    }
  };
  

  handleLetterClick = (letter) => {
    if (this.state.activeGame) {
      if (letter === "enter") {
        this.handleSubmit();
      } else if (letter === "⌫") {
        this.handleLetterDelete();
      } else if (this.state.currentGuess.length < 5) {
        this.setState({
          currentGuess: this.state.currentGuess.concat(letter),
        });
      }
    }
  };

  handleKeyboardClick = (e) => {
    if (this.state.activeGame) {
      const key = e.key;

      if (key === "Enter") {
        this.handleSubmit();
      } else if (key === "Backspace") {
        this.handleLetterDelete();
      } else if (
        this.state.currentGuess.length < 5 &&
        this.validLetters.includes(key.toLowerCase())
      ) {
        this.setState({
          currentGuess: this.state.currentGuess.concat(key.toLowerCase()),
        });
      }
    }
  };

  handleLetterDelete = () => {
    this.setState({ currentGuess: this.state.currentGuess.slice(0, -1) });
  };

  handleCheckGuess = () => {
    const guess = this.state.currentGuess;
    const solution = this.state.solution;

    let newGreenLetters = [];
    let newYellowLetters = [];
    let newGreyLetters = [];

    for (let i = 0; i < 5; i++) {
      if (solution[i] === guess[i]) {
        newGreenLetters.push(guess[i]);
      } else if (solution.includes(guess[i])) {
        newYellowLetters.push(guess[i]);
      } else {
        newGreyLetters.push(guess[i]);
      }
    }

    this.setState({
      letters: {
        green: [...this.state.letters.green, ...newGreenLetters],
        yellow: [...this.state.letters.yellow, ...newYellowLetters],
        grey: [...this.state.letters.grey, ...newGreyLetters],
      },
    });
  };

  handleSubmit = (win = false) => {
    const { currentGuess, solution, wordList, fullList } = this.state;
    if (this.state.activeGame && currentGuess.length === 5) {
      // check if guess is in the wordList or fullList
      if (wordList.includes(currentGuess) || fullList.includes(currentGuess)) {
        // assign new keyboard letter colors
        this.handleCheckGuess();
        // move on to the next guess
        this.setState({
          pastGuesses: [...this.state.pastGuesses, currentGuess],
          currentGuess: "",
        });
  
        // refocus the keyboard to avoid double letter input
        this.puzzleBoardInput.focus();
  
        // check if player won or lost
        if (currentGuess === solution) {
          this.handleWin();
        } else if (this.state.pastGuesses.length === 6) {
          this.handleLoss();
        }
      } else {
        this.handleAlert("¡La palabra no está en la lista!");
  
        // add invalid guess animation
        const guessTiles = document.querySelectorAll(".current-guess");
  
        for (let i = 0; i < guessTiles.length; i++) {
          guessTiles[i].classList.add("invalid-guess");
        }
  
        // remove invalid guess animation
        setTimeout(() => {
          for (let i = 0; i < guessTiles.length; i++) {
            guessTiles[i].classList.remove("invalid-guess");
          }
        }, 250);
      }
    }
  };
  

  handleAlert = (alert) => {
    // push new alert to top of alert list
    this.setState({
      alerts: [alert, ...this.state.alerts],
    });

    const alertList = document.querySelectorAll(".alert");

    // reapply fade after each re-render
    for (let i = 0; i < alertList.length; i++) {
      alertList[i].classList.add("alert-fade");
    }

    // after delay, pop last alert off the list
    setTimeout(() => {
      this.setState({
        alerts: [...this.state.alerts].slice(0, -1),
      });
    }, 10000);
  };

  handleWin = () => {
    this.setState({
      activeGame: false,
    });

    // add winning guess animation
    const winningTiles = document.querySelectorAll(".winning-tile");

    let i = 0;
    const winTileAnimation = () => {
      winningTiles[i].classList.add("winning-guess");
      i++;
      if (i < winningTiles.length) {
        // call function recursively after delay to
        // animate one letter at a time
        setTimeout(() => winTileAnimation(), 250);
      }
    };

    winTileAnimation();

    // find custom win message based on number of attempts
    let winMessage;
    const attempts = this.state.pastGuesses.length;

    switch (attempts) {
      case 1:
        winMessage = "WELL DONE DARLING";
        break;
      case 2:
        winMessage = "NO WAY U DID IT TWICE";
        break;
      case 3:
        winMessage = "3 wins, you are so goated";
        break;
      case 4:
        winMessage = "4 wins?! goodness me you are so talented";
        break;
      case 5:
        winMessage = "5 wins...genuinely how are you doing this";
        break;
      case 6:
        winMessage = "6 whole entire wins I actually love you";
        break;
      default:
        winMessage = "linguists are SO up rn";
    }

    this.handleAlert(winMessage);
  };

  handleHintClick = () => {
      this.handleAlert(`HINT - English translation: ${this.state.englishSolution}`);
  };


  handleLoss = () => {
    this.setState({
      activeGame: false,
    });
    this.handleAlert("¡Se acabaron los intentos!");
  };

  render() {
    return (
      <div
        className="puzzle-board"
        tabIndex={0}
        ref={(inputEl) => (this.puzzleBoardInput = inputEl)}
        onKeyDown={this.handleKeyboardClick}
      >
        <TitleBar />
        <AlertBox alerts={this.state.alerts} />
        <PuzzleGrid
          currentGuess={this.state.currentGuess}
          pastGuesses={this.state.pastGuesses}
          solution={this.state.solution}
        />
        <div style={{padding: "5px"}}> </div>
        <Keyboard
          letters={this.state.letters}
          onLetterClick={this.handleLetterClick}
          onHintClick={this.handleHintClick}  // Added this prop
        />
        
      </div>
    );
  }
  
}

export default PuzzleBoard;
